<template>
    <div class="about">
        <div class="about-top">
            <h4>平台介绍</h4>
            <div>
                <p>{{about.desc}}</p>
            </div>
        </div>
        <div class="about-center">
            <div>
                <img src="../../../static/img/marketing/guanggao4_5.png" alt="">
                <h4>公众号二维码</h4>
                <p>公众号二维码</p>
            </div>
             <div>
                <img src="../../../static/img/marketing/guanggao4_5.png" alt="">
                <h4>官网</h4>
                <p>官网平台</p>
            </div>
        </div>
    </div>
</template>
<script>
import { systemAbout } from '@/api/user.js';
import { mapState } from 'vuex'
export default {
    data(){
        return{
            aboutData:{}
        }
    },
    computed:{
        ...mapState(['about'])
    },
    created(){
        console.log(this.about)
    }
}
</script>
<style lang="scss" scoped>
.about{
    width: 1000px;
    // min-width: 980px;
    margin: 40px auto;
    // padding: 40px;
    .about-top{
        min-height: 260px;
        border:1px solid #e8eae9;
        h4{
            text-align: center;
            line-height: 80px;
        }
        div{
            padding: 50px;
            color: #757575;
        }
    }
    .about-center{
        width: 1000px;
        padding-top: 60px;
        div{
            width: 50%;
            float: left;
            display: flex;
            flex-direction: column;
            justify-content: center;
            padding-left: 50px;
            margin-bottom: 40px;
            img{
                height: 170px;
                width: 400px;
            }
            h4{
                line-height: 50px;
                padding-left: 10px;
            }
            p{
                font-size: 14px;
                color: #757575;
                padding-left: 10px;
            }
        }
    }
}
</style>


